:root{
  --bg:#0b1220;
  --panel:#0f172a;
  --text:#0b1220;
  --muted:#667085;
  --white:#ffffff;
  --brand:#2f8de4;
  --brand-2:#16b0a9;
  --gradient: linear-gradient(90deg, #1a7bd9, #16b0a9);
}

*{box-sizing:border-box}
html,body,#root{height:100%}
body{margin:0;font-family:ui-sans-serif,system-ui,-apple-system,Segoe UI,Roboto,Inter,Arial,sans-serif;color:#0f172a;background:#f8fafc}
a{color:inherit;text-decoration:none}

.container{max-width:1100px;margin:0 auto;padding:0 20px}
.muted{color:var(--muted)}
.navbar{position:sticky;top:0;z-index:50;background:#ffffffbd;backdrop-filter:saturate(180%) blur(10px);border-bottom:1px solid #e6e8eb}
.nav-inner{display:flex;align-items:center;justify-content:space-between;height:64px}
.brand{display:flex;align-items:center;gap:10px;font-weight:700}
.logo-shield{display:inline-flex;width:28px;height:28px;align-items:center;justify-content:center;background:var(--gradient);border-radius:8px;color:#fff;font-size:16px}
.brand-text{font-size:18px}
.nav-links{display:flex;gap:18px;list-style:none;margin:0;padding:0}
.nav-links a{color:#0f172a80;display:inline-flex;align-items:center;gap:8px}
.nav-links a:hover{color:#0f172a}
.nav-icon{display:inline-flex;width:18px;height:18px;align-items:center;justify-content:center}
.auth-actions{display:flex;gap:10px}

.btn{border:1px solid transparent;padding:10px 16px;border-radius:999px;cursor:pointer;font-weight:600}
.btn.primary{background:var(--gradient);color:#fff}
.btn.ghost{background:#fff;border-color:#d0d7de;color:#0f172a}
.btn.ghost:hover{border-color:#b5bec8}

.hero{min-height:80vh;background:
  radial-gradient(1200px 500px at 70% -120px, #e6f2ff, transparent 60%),
  radial-gradient(1000px 500px at -50% -240px, #e7fffb, transparent 60%),
  radial-gradient(400px 240px at 90% 90%, rgba(46,158,255,.12), transparent 60%),
  radial-gradient(360px 220px at 10% 80%, rgba(22,176,169,.10), transparent 70%),
  #f8fafc;
border-bottom:1px solid #e6e8eb}
.hero-inner{display:flex;align-items:center;justify-content:space-between;gap:30px;padding:120px 0}
.hero-copy h1{font-size:44px;line-height:1.1;margin:0 0 12px}
.hero-copy p{color:#475569;margin:0 0 20px;max-width:560px}
.hero-cta{display:flex;gap:12px}

.hero-card{display:flex;align-items:center;gap:16px;background:#fff;border:1px solid #e6e8eb;border-radius:16px;padding:18px 20px;box-shadow:0 16px 50px rgba(2,6,23,.08)}
.hero-card-icon{width:44px;height:44px;border-radius:12px;background:var(--gradient);display:flex;align-items:center;justify-content:center;color:#fff;font-size:22px}
.hero-card-title{margin:0}
.hero-card-sub{margin:4px 0 0;color:#64748b}

/* Hero art image replacing the previous card */
.hero-art{border-radius:16px;overflow:hidden;border:1px solid #e6e8eb;box-shadow:0 16px 50px rgba(2,6,23,.08)}
.hero-art img{display:block;width:520px;max-width:42vw;height:auto}

.section-title{text-align:center;margin:32px 0 6px;font-size:28px}
.section-sub{text-align:center;margin:0 0 24px;color:#64748b}
.cards{display:grid;grid-template-columns:repeat(3,minmax(0,1fr));gap:18px}
.card{background:#fff;border:1px solid #e6e8eb;border-radius:14px;padding:18px;display:flex;flex-direction:column;gap:8px}
.card-icon{width:36px;height:36px;border-radius:10px;background:#eef6ff;color:#1a7bd9;display:flex;align-items:center;justify-content:center}
.card-title{margin:4px 0}
.card-text{color:#64748b;margin:0}
.card-link{margin-top:auto;color:#1a7bd9}

.trust{margin:18px 0}
.trust-row{display:grid;grid-template-columns:repeat(3,minmax(0,1fr));gap:12px}
.trust-item{display:flex;align-items:center;justify-content:center;background:#fff;border:1px solid #e6e8eb;border-radius:12px;height:56px;color:#0f172a;font-weight:600}

.how-it-works{margin-top:40px}
.how-it-works .steps{display:grid;grid-template-columns:repeat(4,minmax(0,1fr));gap:18px}
.step{background:#fff;border:1px solid #e6e8eb;border-radius:14px;padding:18px;text-align:center}
.step-icon{width:44px;height:44px;margin:0 auto 8px;border-radius:12px;background:#eef6ff;color:#1a7bd9;display:flex;align-items:center;justify-content:center;font-size:22px}
.step-title{margin:6px 0}
.step-text{color:#64748b;margin:0}

/* Reveal base state */
.reveal{opacity:0;transform:translateY(16px);transition:opacity .6s ease, transform .6s ease}
.reveal.show{opacity:1;transform:none}

/* Subtle hovers */
.btn{transition:transform .15s ease, box-shadow .2s ease}
.btn:hover{transform:translateY(-1px);box-shadow:0 8px 24px rgba(2,6,23,.08)}
.card,.step,.hero-card{transition:transform .2s ease, box-shadow .2s ease}
.card:hover,.step:hover,.hero-card:hover{transform:translateY(-3px);box-shadow:0 18px 40px rgba(2,6,23,.12)}

.stats{background:var(--gradient);color:#fff;margin:28px 0}
.stat-grid{display:grid;grid-template-columns:repeat(4,minmax(0,1fr));gap:16px;padding:30px 0}
.stat{display:flex;flex-direction:column;align-items:center}
.stat-value{font-size:26px;font-weight:800}
.stat-label{opacity:.9}

.partners .partner-row{display:grid;grid-template-columns:repeat(4,minmax(0,1fr));gap:12px;margin:12px 0 28px}
.partner-pill{display:flex;align-items:center;justify-content:center;background:#fff;border:1px solid #e6e8eb;border-radius:12px;height:48px}

.why{margin-top:40px}
.why-grid{display:grid;grid-template-columns:repeat(3,minmax(0,1fr));gap:24px;margin:22px 0 36px}
.why-item{display:flex;gap:14px;background:#fff;border:1px solid #e6e8eb;border-radius:16px;padding:20px}
.why-icon{width:38px;height:38px;border-radius:10px;background:#eef6ff;color:#1a7bd9;display:flex;align-items:center;justify-content:center}
.why-title{margin:0}
.why-text{margin:6px 0 0;color:#64748b}
.chip{display:inline-block;margin-top:8px;padding:4px 8px;border:1px solid #d0d7de;border-radius:999px;font-size:12px;color:#475569;background:#f6f9ff}

.compare{border:1px solid #e6e8eb;border-radius:16px;overflow:hidden;background:#fff;margin:18px 0 28px;box-shadow:0 8px 24px rgba(2,6,23,.05)}
.cmp-row{display:grid;grid-template-columns:2fr 1.2fr 1.2fr}
.cmp-head{background:#f2f6fb;font-weight:700}
.cmp-cell{padding:16px 18px;border-bottom:1px solid #e6e8eb}
.cmp-cell.ours{color:#0f172a;font-weight:700}

@media (max-width: 900px){
  .why-grid{grid-template-columns:repeat(2,minmax(0,1fr));gap:18px}
  .cmp-row{grid-template-columns:1.4fr 1fr 1fr}
}
@media (max-width: 560px){
  .why-grid{grid-template-columns:1fr;gap:16px}
}

.footer{background:#0b1220;color:#e6edf6;margin-top:32px;padding-top:28px}
.footer-grid{display:grid;grid-template-columns:2fr 1fr 1fr 1fr;gap:20px;padding-bottom:18px}
.footer a{color:#c6d3e0}
.footer a:hover{color:#fff}
.footer-brand .brand-text{color:#fff}
.footer-bottom{display:flex;align-items:center;justify-content:space-between;border-top:1px solid #1f2a44;padding:14px 0;color:#9fb0c3}

.modal{border:none;border-radius:16px;padding:0}
.modal-card{display:flex;flex-direction:column;gap:10px;padding:20px;min-width:320px}
.modal-card label{display:flex;flex-direction:column;gap:6px;font-size:14px}
.modal-card input{border:1px solid #d0d7de;border-radius:10px;padding:10px}

/* Icons page */
.icons-page{padding:32px 0}
.icons-grid{display:grid;grid-template-columns:repeat(6,minmax(0,1fr));gap:16px}
.icon-card{background:#fff;border:1px solid #e6e8eb;border-radius:14px;padding:16px;display:flex;flex-direction:column;align-items:center;gap:10px;text-align:center}
.icon-badge{width:44px;height:44px;border-radius:12px;background:#eef6ff;color:#1a7bd9;display:flex;align-items:center;justify-content:center}
.icon-name{font-size:14px;color:#475569}

/* About page */
.about-hero{min-height:60vh;background:
  radial-gradient(1000px 400px at 80% -100px, #e6f2ff, transparent 60%),
  radial-gradient(800px 360px at -40% -160px, #e7fffb, transparent 60%),
  #f8fafc;border-bottom:1px solid #e6e8eb}
.about-hero-inner{display:flex;align-items:center;justify-content:center;gap:30px;padding:80px 0}
.about-hero-copy h1{font-size:38px;margin:0 0 10px}
.about-hero-copy p{color:#475569;margin:0 0 16px;max-width:560px}
.about-page .about-hero-copy{text-align:center}
.about-hero-art img{width:480px;max-width:42vw;border-radius:16px;border:1px solid #e6e8eb;box-shadow:0 16px 50px rgba(2,6,23,.08)}
.about-section{margin:34px 0}
/* Center align all About page text */
.about-page .section-title,.about-page .section-sub{text-align:center;margin-left:auto;margin-right:auto}
.about-page .card,.about-page .step,.about-page .gallery-item figcaption,.about-page .values-list li{
  text-align:center;
}
.about-page .why-item{flex-direction:column;align-items:center;text-align:center}

.about-gallery .gallery-grid{display:grid;grid-template-columns:repeat(4,minmax(0,1fr));gap:14px}
.gallery-item{background:#fff;border:1px solid #e6e8eb;border-radius:14px;overflow:hidden}
.gallery-item img{width:100%;height:160px;object-fit:cover;display:block;transition:transform .35s ease}
.gallery-item:hover img{transform:scale(1.05)}
.gallery-item figcaption{padding:10px 12px;color:#475569}

.tilt{transform:perspective(800px) rotateX(0) rotateY(0);transition:transform .3s ease}
.tilt:hover{transform:perspective(800px) rotateX(2deg) rotateY(-2deg) scale(1.01)}

.impact{background:var(--gradient);color:#fff;margin:28px 0}
.values .values-list{list-style:none;padding:0;margin:18px 0;display:grid;grid-template-columns:repeat(2,minmax(0,1fr));gap:10px}
.values .values-list li{background:#fff;border:1px solid #e6e8eb;border-radius:12px;padding:10px 12px;display:flex;gap:10px;align-items:center}
.values .values-list li span{display:inline-flex;width:22px;height:22px;align-items:center;justify-content:center}

.privacy-leadership .split{display:grid;grid-template-columns:1fr 1fr;gap:18px}
.cta-block{padding:28px 0}
.cta-card{display:flex;align-items:center;justify-content:space-between;gap:14px;background:#fff;border:1px solid #e6e8eb;border-radius:16px;padding:18px 20px;box-shadow:0 16px 50px rgba(2,6,23,.08)}
.cta-actions{display:flex;gap:10px}

@media (max-width: 900px){
  .about-hero-inner{flex-direction:column}
  .about-hero-art img{width:100%;max-width:100%}
  .about-gallery .gallery-grid{grid-template-columns:repeat(2,minmax(0,1fr))}
  .privacy-leadership .split{grid-template-columns:1fr}
}
@media (max-width: 560px){
  .about-gallery .gallery-grid{grid-template-columns:1fr}
  .values .values-list{grid-template-columns:1fr}
}

/* Catalog (Udemy-like) */
.catalog .catalog-tabs{display:flex;gap:18px;overflow:auto;padding-bottom:8px;border-bottom:1px solid #e6e8eb}
.catalog-tab{border:none;background:transparent;font-weight:800;color:#0f172a;padding:8px 0;cursor:pointer;white-space:nowrap}
.catalog-tab.active{border-bottom:3px solid #0f172a}
.catalog-pills{display:flex;gap:18px;overflow:auto;padding:16px 0}
.pill{min-width:220px;background:#eef2ff;border:1px solid #e6e8eb;border-radius:999px;padding:16px 18px;display:flex;flex-direction:column;gap:4px}
.pill-title{font-weight:800;color:#0f172a}
.pill-sub{color:#64748b}

/* Resources page (distinct look) */
.resources-hero{min-height:46vh;background:#0b1220;color:#e6edf6;border-bottom:1px solid #1f2a44}
.resources-hero-inner{display:flex;align-items:center;justify-content:space-between;gap:26px;padding:60px 0}
.resources-copy h1{margin:0 0 8px}
.resources-copy p{color:#c6d3e0;margin:0}
.resources-art img{width:420px;max-width:40vw;border-radius:16px;border:1px solid #1f2a44}

.tabs-row{display:flex;flex-wrap:wrap;gap:8px;margin:18px 0}
.tab-pill{border:1px solid #d0d7de;background:#fff;border-radius:999px;padding:8px 12px;font-weight:600;color:#0f172a;cursor:pointer}
.tab-pill.active{background:#0f172a;color:#fff;border-color:#0f172a}

.resource-section{margin:16px 0}
.grid-3{display:grid;grid-template-columns:repeat(3,minmax(0,1fr));gap:14px}
.tile{background:#fff;border:1px solid #e6e8eb;border-radius:14px;overflow:hidden}
.tile img{width:100%;height:160px;object-fit:cover;display:block;transition:transform .35s ease}
.tile:hover img{transform:scale(1.05)}
.tile-meta{padding:10px 12px}
.tile-title{font-weight:700}
.tile-sub{color:#64748b}

.accordion{border:1px solid #e6e8eb;border-radius:12px;margin:10px 0;background:#fff}
.accordion-head{width:100%;text-align:left;padding:12px 14px;border:none;background:transparent;display:flex;align-items:center;justify-content:space-between;font-weight:700;cursor:pointer}
.accordion-body{padding:0 14px 12px 14px;color:#475569}
.chev{opacity:.6}

.doc-card{display:flex;align-items:center;gap:10px;background:#fff;border:1px solid #e6e8eb;border-radius:12px;padding:12px}
.doc-icon{width:28px;height:28px;border-radius:8px;background:#eef6ff;color:#1a7bd9;display:flex;align-items:center;justify-content:center}
.doc-title{font-weight:700}
.doc-desc{color:#64748b}
.doc-action{margin-left:auto;color:#1a7bd9}

@media (max-width: 900px){
  .resources-hero-inner{flex-direction:column}
  .resources-art img{width:100%;max-width:100%}
  .grid-3{grid-template-columns:1fr 1fr}
}
@media (max-width: 560px){
  .grid-3{grid-template-columns:1fr}
}

/* Pricing Section */
.pricing-grid{display:grid;grid-template-columns:repeat(3,minmax(0,1fr));gap:20px;margin:18px 0}
.pricing-card{background:#fff;border:1px solid #e6e8eb;border-radius:16px;padding:24px;position:relative;transition:transform .2s ease, box-shadow .2s ease}
.pricing-card:hover{transform:translateY(-4px);box-shadow:0 20px 40px rgba(2,6,23,.12)}
.pricing-card.popular{border-color:#1a7bd9;box-shadow:0 8px 24px rgba(26,123,217,.15)}
.popular-badge{position:absolute;top:-12px;left:50%;transform:translateX(-50%);background:var(--gradient);color:#fff;padding:4px 12px;border-radius:999px;font-size:12px;font-weight:700}
.pricing-header{text-align:center;margin-bottom:20px}
.plan-name{font-size:20px;margin:0 0 8px;color:#0f172a}
.price{display:flex;align-items:baseline;justify-content:center;gap:4px}
.price-amount{font-size:32px;font-weight:800;color:#0f172a}
.price-period{color:#64748b}
.features-list{list-style:none;padding:0;margin:0 0 20px}
.features-list li{padding:6px 0;color:#475569}
.pricing-note{text-align:center;margin-top:16px;color:#64748b}

/* Trial CTA Section */
.trial-cta{background:var(--gradient);color:#fff;margin:28px 0}
.trial-content{text-align:center;padding:40px 0}
.trial-content h2{font-size:32px;margin:0 0 12px}
.trial-content p{font-size:18px;margin:0 0 24px;opacity:.9}
.trial-actions{display:flex;gap:12px;justify-content:center;margin-bottom:24px}
.btn.large{padding:14px 24px;font-size:16px}
.trial-stats{display:flex;justify-content:center;gap:32px;flex-wrap:wrap}
.stat-item{display:flex;flex-direction:column;align-items:center;gap:4px}
.stat-number{font-size:24px;font-weight:800}
.stat-text{font-size:14px;opacity:.8}

/* Newsletter Section */
.newsletter-content{text-align:center;padding:40px 0}
.newsletter-content h2{font-size:28px;margin:0 0 12px}
.newsletter-content p{color:#64748b;margin:0 0 20px;max-width:500px;margin-left:auto;margin-right:auto}
.newsletter-form{max-width:400px;margin:0 auto}
.newsletter-input{display:flex;gap:8px}
.newsletter-input input{flex:1;border:1px solid #d0d7de;border-radius:10px;padding:12px}
.newsletter-success{display:flex;align-items:center;gap:8px;justify-content:center;color:#059669;font-weight:600}

/* Webinar Section */
.webinar-grid{display:grid;grid-template-columns:repeat(3,minmax(0,1fr));gap:18px;margin:18px 0}
.webinar-card{background:#fff;border:1px solid #e6e8eb;border-radius:14px;overflow:hidden;transition:transform .2s ease, box-shadow .2s ease}
.webinar-card:hover{transform:translateY(-3px);box-shadow:0 16px 40px rgba(2,6,23,.1)}
.webinar-image{width:100%;height:120px;background:#eef6ff;display:flex;align-items:center;justify-content:center;font-size:48px}
.webinar-content{padding:16px}
.webinar-title{font-size:16px;margin:0 0 8px;font-weight:700}
.webinar-meta{color:#64748b;font-size:14px;margin-bottom:8px}
.webinar-speaker{font-style:italic}
.webinar-spots{color:#059669;font-size:12px;font-weight:600;margin-bottom:12px}
.btn.small{padding:8px 12px;font-size:14px}

/* Demo Modal */
.modal-overlay{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,.5);display:flex;align-items:center;justify-content:center;z-index:1000;padding:20px}
.modal-content{background:#fff;border-radius:16px;max-width:500px;width:100%;max-height:80vh;overflow:hidden}
.modal-header{display:flex;align-items:center;justify-content:space-between;padding:20px;border-bottom:1px solid #e6e8eb}
.modal-header h3{margin:0}
.modal-close{background:none;border:none;font-size:24px;cursor:pointer;color:#64748b}
.modal-body{padding:20px}
.demo-placeholder{text-align:center;padding:20px}
.demo-placeholder span{font-size:48px;display:block;margin-bottom:16px}
.demo-placeholder p{margin:8px 0}

/* Responsive adjustments */
@media (max-width: 900px){
  .pricing-grid{grid-template-columns:repeat(2,minmax(0,1fr))}
  .webinar-grid{grid-template-columns:repeat(2,minmax(0,1fr))}
  .trial-actions{flex-direction:column;align-items:center}
}
@media (max-width: 560px){
  .pricing-grid{grid-template-columns:1fr}
  .webinar-grid{grid-template-columns:1fr}
  .trial-stats{flex-direction:column;gap:16px}
  .newsletter-input{flex-direction:column}
}

/* Help page */
.help-hero{min-height:40vh;background:linear-gradient(90deg,#1a7bd9,#16b0a9);color:#fff}
.help-hero-inner{display:flex;align-items:center;justify-content:space-between;gap:26px;padding:50px 0}
.help-copy h1{margin:0 0 8px}
.help-copy p{margin:0 0 14px;color:#e6edf6}
.help-search{display:flex;align-items:center;gap:10px;background:rgba(255,255,255,.1);border:1px solid rgba(255,255,255,.25);border-radius:12px;padding:10px 12px}
.help-search input{flex:1;border:none;background:transparent;color:#fff;outline:none}
.help-search input::placeholder{color:#dbeafe}
.help-quicklinks{display:flex;gap:10px;margin-top:10px;flex-wrap:wrap}
.help-quicklinks .ql{background:#fff;color:#0f172a;border-radius:999px;padding:8px 12px;font-weight:700}
.help-art{font-size:64px;filter:drop-shadow(0 10px 30px rgba(0,0,0,.25))}
.help-content{padding:26px 0}
.faq-list{display:flex;flex-direction:column;gap:10px}
.faq{background:#fff;border:1px solid #e6e8eb;border-radius:12px;overflow:hidden}
.faq-head{width:100%;text-align:left;padding:12px 14px;border:none;background:transparent;display:flex;align-items:center;justify-content:space-between;font-weight:800;cursor:pointer}
.faq-body{padding:0 14px 14px 14px;color:#475569;display:none}
.faq.open .faq-body{display:block}
.no-results{display:flex;align-items:center;gap:10px;background:#fff;border:1px solid #e6e8eb;border-radius:12px;padding:14px}

/* Contact page */
.contact-hero{min-height:32vh;background:linear-gradient(90deg,#1a7bd9,#16b0a9);color:#fff}
.contact-hero-inner{display:flex;align-items:center;justify-content:flex-start;padding:40px 0}
.contact-content{padding:28px 0}
.contact-grid{display:grid;grid-template-columns:1.2fr 1fr;gap:20px}
.contact-card{background:#fff;border:1px solid #e6e8eb;border-radius:14px;padding:14px}
.info-row{display:flex;align-items:center;gap:10px;border-bottom:1px solid #e6e8eb;padding:10px 0}
.info-row:last-child{border-bottom:none}
.info-label{width:140px;color:#64748b;font-weight:600}
.info-value{color:#0f172a}
.contact-form select,.contact-form textarea{border:1px solid #d0d7de;border-radius:10px;padding:12px}
.contact-success{background:#fff;border:1px solid #e6e8eb;border-radius:14px;padding:18px;text-align:center}
.success-icon{font-size:38px}

@media (max-width: 900px){
  .contact-grid{grid-template-columns:1fr}
}

/* Auth screens */
.auth-screen{background:linear-gradient(rgba(6,12,23,.45),rgba(6,12,23,.45)),url('https://images.unsplash.com/photo-1513258496099-48168024aec0?q=80&w=1600&auto=format&fit=crop') center/cover no-repeat fixed;min-height:calc(100vh - 64px);display:flex;align-items:center;justify-content:center;padding:24px}
.auth-card{width:540px;max-width:92vw;background:rgba(255,255,255,.9);backdrop-filter:blur(8px);border:1px solid #e6e8eb;border-radius:16px;box-shadow:0 20px 60px rgba(2,6,23,.12);padding:24px;display:flex;flex-direction:column;gap:14px}
.role-tabs{display:flex;gap:10px;margin-top:6px}
.tab{flex:1;border:1px solid #d0d7de;background:#f8fafc;border-radius:12px;padding:10px 12px;cursor:pointer;font-weight:600;color:#475569}
.tab.active{border-color:#7ec8ff;background:#eaf5ff;color:#0f172a;box-shadow:0 0 0 3px #e1f0ff inset}
.form{display:flex;flex-direction:column;gap:12px}
.grid-2{display:grid;grid-template-columns:1fr 1fr;gap:12px}
.form label{display:flex;flex-direction:column;gap:6px;font-size:14px}
.form input{border:1px solid #d0d7de;border-radius:10px;padding:12px}
.form-row{display:flex;align-items:center;justify-content:space-between}
.checkbox{display:flex;align-items:center;gap:8px}

@media (max-width: 900px){
  .cards{grid-template-columns:repeat(2,minmax(0,1fr))}
  .stat-grid{grid-template-columns:repeat(2,minmax(0,1fr))}
  .partners .partner-row{grid-template-columns:repeat(2,minmax(0,1fr))}
  .hero-inner{flex-direction:column;align-items:flex-start}
}
@media (max-width: 560px){
  .cards{grid-template-columns:1fr}
  .footer-grid{grid-template-columns:1fr}
  .nav-links{display:none}
}
